import React, { useState } from 'react';
import { Trophy, TrendingUp, Clock, Medal, Crown } from 'lucide-react';

const LeaderboardPage: React.FC = () => {
  const [activeTab, setActiveTab] = useState<'wins' | 'walks'>('wins');
  const [timePeriod, setTimePeriod] = useState<'daily' | 'weekly' | 'all-time'>('daily');

  // Mock leaderboard data - in real app this would come from API
  const mockLeaderboard = {
    wins: [
      { rank: 1, username: 'LuckyDog123', value: '$125.50', avatar: '🐕' },
      { rank: 2, username: 'WalkMaster', value: '$98.25', avatar: '🦮' },
      { rank: 3, username: 'SquirrelDodger', value: '$87.75', avatar: '🐶' },
      { rank: 4, username: 'CashOutKing', value: '$76.00', avatar: '🐕‍🦺' },
      { rank: 5, username: 'RiskTaker', value: '$65.50', avatar: '🐩' }
    ],
    walks: [
      { rank: 1, username: 'MarathonWalker', value: '28.5s', avatar: '🦮' },
      { rank: 2, username: 'PatiencePlayer', value: '27.2s', avatar: '🐕' },
      { rank: 3, username: 'NerveOfSteel', value: '26.8s', avatar: '🐶' },
      { rank: 4, username: 'TimingMaster', value: '25.1s', avatar: '🐕‍🦺' },
      { rank: 5, username: 'LongWalker', value: '24.7s', avatar: '🐩' }
    ]
  };

  const getRankIcon = (rank: number) => {
    switch (rank) {
      case 1:
        return <Crown className="text-yellow-400" size={20} />;
      case 2:
        return <Medal className="text-gray-300" size={20} />;
      case 3:
        return <Medal className="text-amber-600" size={20} />;
      default:
        return <span className="text-gray-400 font-bold text-lg">#{rank}</span>;
    }
  };

  const getRankBg = (rank: number) => {
    switch (rank) {
      case 1:
        return 'bg-gradient-to-r from-yellow-500/20 to-yellow-600/20 border-yellow-400/30';
      case 2:
        return 'bg-gradient-to-r from-gray-400/20 to-gray-500/20 border-gray-400/30';
      case 3:
        return 'bg-gradient-to-r from-amber-600/20 to-amber-700/20 border-amber-600/30';
      default:
        return 'bg-gray-800 border-gray-700';
    }
  };

  const currentData = mockLeaderboard[activeTab];

  return (
    <div className="max-w-4xl mx-auto space-y-8">
      {/* Header */}
      <div className="text-center">
        <h1 className="text-3xl md:text-4xl font-bold mb-4">
          <span className="gradient-text">Leaderboards</span>
        </h1>
        <p className="text-gray-300 text-lg">
          See how you stack up against other players
        </p>
      </div>

      {/* Controls */}
      <div className="flex flex-col sm:flex-row gap-4 items-center justify-between">
        {/* Category Tabs */}
        <div className="flex bg-gray-800 rounded-lg p-1">
          <button
            onClick={() => setActiveTab('wins')}
            className={`px-4 py-2 rounded-md font-medium transition-colors flex items-center space-x-2 ${
              activeTab === 'wins'
                ? 'bg-blue-600 text-white'
                : 'text-gray-300 hover:text-white'
            }`}
          >
            <TrendingUp size={18} />
            <span>Top Wins</span>
          </button>
          <button
            onClick={() => setActiveTab('walks')}
            className={`px-4 py-2 rounded-md font-medium transition-colors flex items-center space-x-2 ${
              activeTab === 'walks'
                ? 'bg-blue-600 text-white'
                : 'text-gray-300 hover:text-white'
            }`}
          >
            <Clock size={18} />
            <span>Longest Walks</span>
          </button>
        </div>

        {/* Time Period Filter */}
        <div className="flex bg-gray-800 rounded-lg p-1">
          {(['daily', 'weekly', 'all-time'] as const).map((period) => (
            <button
              key={period}
              onClick={() => setTimePeriod(period)}
              className={`px-3 py-2 rounded-md text-sm font-medium transition-colors ${
                timePeriod === period
                  ? 'bg-gray-600 text-white'
                  : 'text-gray-300 hover:text-white'
              }`}
            >
              {period === 'all-time' ? 'All Time' : period.charAt(0).toUpperCase() + period.slice(1)}
            </button>
          ))}
        </div>
      </div>

      {/* Leaderboard */}
      <div className="space-y-3">
        {currentData.map((entry) => (
          <div
            key={entry.rank}
            className={`p-4 rounded-lg border transition-all hover:scale-[1.02] ${getRankBg(entry.rank)}`}
          >
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-4">
                {/* Rank */}
                <div className="flex items-center justify-center w-10 h-10">
                  {getRankIcon(entry.rank)}
                </div>

                {/* Avatar */}
                <div className="text-2xl">
                  {entry.avatar}
                </div>

                {/* User Info */}
                <div>
                  <h3 className="font-bold text-lg">{entry.username}</h3>
                  <p className="text-gray-400 text-sm">
                    {activeTab === 'wins' ? 'Top Win' : 'Longest Walk'}
                  </p>
                </div>
              </div>

              {/* Value */}
              <div className="text-right">
                <div className={`text-xl font-bold ${
                  entry.rank === 1 ? 'text-yellow-400' :
                  entry.rank === 2 ? 'text-gray-300' :
                  entry.rank === 3 ? 'text-amber-600' :
                  'text-blue-400'
                }`}>
                  {entry.value}
                </div>
                <p className="text-gray-400 text-sm">
                  {timePeriod === 'all-time' ? 'All Time' : timePeriod}
                </p>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Your Rank */}
      <div className="card bg-blue-900/20 border-blue-500/50">
        <h2 className="text-xl font-bold mb-4 flex items-center space-x-2 text-blue-400">
          <Trophy size={24} />
          <span>Your Ranking</span>
        </h2>
        
        <div className="text-center py-8 text-gray-400">
          <p className="mb-2">You haven't played any games yet!</p>
          <p className="text-sm">Play some games to appear on the leaderboards.</p>
          <button className="text-blue-400 hover:text-blue-300 transition-colors mt-3 px-6 py-2 bg-blue-600/20 rounded-lg">
            Start Playing →
          </button>
        </div>
      </div>

      {/* Hall of Fame */}
      <div className="card">
        <h2 className="text-xl font-bold mb-6 flex items-center space-x-2">
          <Crown className="text-yellow-400" size={24} />
          <span>Hall of Fame</span>
        </h2>
        
        <div className="grid md:grid-cols-3 gap-4">
          {/* Biggest Win */}
          <div className="text-center p-4 bg-yellow-900/20 rounded-lg border border-yellow-500/30">
            <div className="text-3xl mb-2">👑</div>
            <h3 className="font-bold text-yellow-400 mb-1">Biggest Win</h3>
            <p className="text-2xl font-bold mb-1">$250.00</p>
            <p className="text-gray-400 text-sm">by LegendaryPlayer</p>
          </div>

          {/* Longest Walk */}
          <div className="text-center p-4 bg-blue-900/20 rounded-lg border border-blue-500/30">
            <div className="text-3xl mb-2">⏱️</div>
            <h3 className="font-bold text-blue-400 mb-1">Longest Walk</h3>
            <p className="text-2xl font-bold mb-1">29.8s</p>
            <p className="text-gray-400 text-sm">by PatienceMaster</p>
          </div>

          {/* Most Games */}
          <div className="text-center p-4 bg-purple-900/20 rounded-lg border border-purple-500/30">
            <div className="text-3xl mb-2">🎮</div>
            <h3 className="font-bold text-purple-400 mb-1">Most Games</h3>
            <p className="text-2xl font-bold mb-1">1,247</p>
            <p className="text-gray-400 text-sm">by GameAddict</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LeaderboardPage; 