import React, { useState, useEffect } from 'react';
import { Bitcoin, Copy, CheckCircle, AlertCircle } from 'lucide-react';
import LoadingSpinner from '../ui/LoadingSpinner';

interface DepositModalProps {
  isOpen: boolean;
  onClose: () => void;
  onDepositComplete: (amount: number, currency: string) => void;
}

interface DepositAddress {
  address: string;
  currency: 'BTC' | 'ETH';
  qrCode: string;
}

const DepositModal: React.FC<DepositModalProps> = ({ isOpen, onClose, onDepositComplete }) => {
  const [selectedCurrency, setSelectedCurrency] = useState<'BTC' | 'ETH'>('BTC');
  const [depositAddress, setDepositAddress] = useState<DepositAddress | null>(null);
  const [loading, setLoading] = useState(false);
  const [copied, setCopied] = useState(false);
  const [monitoringDeposit, setMonitoringDeposit] = useState(false);

  // Cryptocurrency prices (would be fetched from API)
  const cryptoPrices = {
    BTC: 45000,
    ETH: 2800
  };

  useEffect(() => {
    if (isOpen) {
      generateDepositAddress();
    }
  }, [isOpen, selectedCurrency]);

  const generateDepositAddress = async () => {
    setLoading(true);
    try {
      // Simulate API call to generate deposit address
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      const mockAddress = selectedCurrency === 'BTC' 
        ? 'bc1qw508d6qejxtdg4y5r3zarvary0c5xw7kv8f3t4'
        : '0x742d35Cc4ccf1a3C5e0b1E4f5C7b6Ae6E1B0C4FF5';
      
      setDepositAddress({
        address: mockAddress,
        currency: selectedCurrency,
        qrCode: `data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="100" height="100"><rect width="100" height="100" fill="white"/><text x="50" y="50" text-anchor="middle" dy=".3em" fill="black">QR</text></svg>`
      });
      
      // Start monitoring for deposits
      startDepositMonitoring(mockAddress);
    } catch (error) {
      console.error('Error generating deposit address:', error);
    } finally {
      setLoading(false);
    }
  };

  const startDepositMonitoring = (_address: string) => {
    setMonitoringDeposit(true);
    
    // Simulate deposit detection (in production, use websockets or polling)
    setTimeout(() => {
      // Simulate a deposit being detected
      const depositAmount = 0.001; // Example amount
      onDepositComplete(depositAmount, selectedCurrency);
      setMonitoringDeposit(false);
      onClose();
    }, 10000); // Simulate 10 second delay
  };

  const copyToClipboard = async () => {
    if (depositAddress) {
      await navigator.clipboard.writeText(depositAddress.address);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    }
  };

  const refreshAddress = () => {
    generateDepositAddress();
  };

  if (!isOpen) return null;

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <div className="p-6">
          {/* Header */}
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-2xl font-bold text-white">Deposit Funds</h2>
            <button
              onClick={onClose}
              className="text-gray-400 hover:text-white text-2xl"
            >
              ×
            </button>
          </div>

          {/* Currency Selection */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-300 mb-3">
              Select Cryptocurrency
            </label>
            <div className="grid grid-cols-2 gap-3">
              <button
                onClick={() => setSelectedCurrency('BTC')}
                className={`p-4 rounded-lg border-2 transition-all ${
                  selectedCurrency === 'BTC'
                    ? 'border-orange-500 bg-orange-500/10'
                    : 'border-gray-600 bg-gray-700/50'
                }`}
              >
                <div className="flex items-center justify-center space-x-2">
                  <Bitcoin className="text-orange-400" size={24} />
                  <div className="text-left">
                    <p className="font-bold text-white">Bitcoin</p>
                    <p className="text-sm text-gray-400">${cryptoPrices.BTC.toLocaleString()}</p>
                  </div>
                </div>
              </button>
              
              <button
                onClick={() => setSelectedCurrency('ETH')}
                className={`p-4 rounded-lg border-2 transition-all ${
                  selectedCurrency === 'ETH'
                    ? 'border-purple-500 bg-purple-500/10'
                    : 'border-gray-600 bg-gray-700/50'
                }`}
              >
                <div className="flex items-center justify-center space-x-2">
                  <div className="w-6 h-6 bg-gradient-to-r from-purple-400 to-blue-400 rounded"></div>
                  <div className="text-left">
                    <p className="font-bold text-white">Ethereum</p>
                    <p className="text-sm text-gray-400">${cryptoPrices.ETH.toLocaleString()}</p>
                  </div>
                </div>
              </button>
            </div>
          </div>

          {/* Deposit Address */}
          {loading ? (
            <div className="text-center py-8">
              <LoadingSpinner size="large" />
              <p className="text-gray-400 mt-4">Generating secure deposit address...</p>
            </div>
          ) : depositAddress ? (
            <div className="space-y-4">
              {/* QR Code */}
              <div className="bg-white p-4 rounded-lg text-center">
                <img 
                  src={depositAddress.qrCode} 
                  alt="Deposit QR Code"
                  className="mx-auto mb-2"
                  width="150"
                  height="150"
                />
                <p className="text-gray-600 text-sm">Scan with your wallet</p>
              </div>

              {/* Address */}
              <div>
                <label className="block text-sm font-medium text-gray-300 mb-2">
                  {selectedCurrency} Deposit Address
                </label>
                <div className="flex items-center space-x-2">
                  <input
                    type="text"
                    value={depositAddress.address}
                    readOnly
                    className="input-field flex-1 font-mono text-sm"
                  />
                  <button
                    onClick={copyToClipboard}
                    className="btn-secondary py-3 px-4 flex items-center space-x-2"
                  >
                    {copied ? <CheckCircle size={16} /> : <Copy size={16} />}
                    <span>{copied ? 'Copied!' : 'Copy'}</span>
                  </button>
                </div>
              </div>

              {/* Instructions */}
              <div className="bg-blue-900/20 border border-blue-500/30 rounded-lg p-4">
                <h3 className="font-bold text-blue-400 mb-2 flex items-center space-x-2">
                  <AlertCircle size={16} />
                  <span>Deposit Instructions</span>
                </h3>
                <ul className="text-sm text-gray-300 space-y-1">
                  <li>• Send {selectedCurrency} to the address above</li>
                  <li>• Minimum deposit: {selectedCurrency === 'BTC' ? '0.0001 BTC' : '0.001 ETH'}</li>
                  <li>• Funds will be credited after 3 network confirmations</li>
                  <li>• This address is unique to your account</li>
                </ul>
              </div>

              {/* Monitoring Status */}
              {monitoringDeposit && (
                <div className="bg-yellow-900/20 border border-yellow-500/30 rounded-lg p-4">
                  <div className="flex items-center space-x-3">
                    <LoadingSpinner size="small" />
                    <div>
                      <p className="font-bold text-yellow-400">Monitoring for deposits...</p>
                      <p className="text-sm text-gray-300">
                        We'll automatically detect when you send {selectedCurrency} to this address
                      </p>
                    </div>
                  </div>
                </div>
              )}

              {/* Actions */}
              <div className="flex space-x-3">
                <button
                  onClick={refreshAddress}
                  className="btn-secondary flex-1 py-3"
                >
                  Generate New Address
                </button>
                <button
                  onClick={onClose}
                  className="btn-primary flex-1 py-3"
                >
                  Done
                </button>
              </div>
            </div>
          ) : null}

          {/* Security Notice */}
          <div className="mt-6 p-3 bg-gray-900/50 rounded-lg border border-gray-700">
            <p className="text-xs text-gray-400 text-center">
              🔒 This address is securely generated and unique to your account. 
              Never share your private keys with anyone.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DepositModal; 