import React from 'react';
import { useAuth } from '../hooks/useAuth';
import { User, Mail, Calendar, TrendingUp, Trophy, Target, Clock } from 'lucide-react';

const ProfilePage: React.FC = () => {
  const { user } = useAuth();

  // Mock statistics - in real app these would come from API
  const stats = {
    totalGames: 0,
    totalWins: 0,
    totalLosses: 0,
    winRate: 0,
    totalWagered: 0,
    totalWinnings: 0,
    longestWalk: 0,
    biggestWin: 0
  };

  const achievements = [
    { id: 1, name: 'First Steps', description: 'Play your first game', unlocked: false, icon: '🐾' },
    { id: 2, name: 'Lucky Dog', description: 'Win 10 games', unlocked: false, icon: '🍀' },
    { id: 3, name: 'Marathon Walker', description: 'Walk for 25+ seconds', unlocked: false, icon: '🏃' },
    { id: 4, name: 'High Roller', description: 'Deposit $100+', unlocked: false, icon: '💰' },
    { id: 5, name: 'Squirrel Dodger', description: 'Cash out at 15+ seconds', unlocked: false, icon: '🐿️' }
  ];

  return (
    <div className="max-w-4xl mx-auto space-y-8">
      {/* Profile Header */}
      <div className="card">
        <div className="flex items-center space-x-6">
          <div className="w-20 h-20 bg-gradient-to-r from-blue-500 to-purple-500 rounded-full flex items-center justify-center text-2xl font-bold">
            {user?.username?.charAt(0).toUpperCase()}
          </div>
          <div className="flex-1">
            <h1 className="text-2xl font-bold mb-2">{user?.username}</h1>
            <div className="space-y-1 text-gray-300">
              <div className="flex items-center space-x-2">
                <Mail size={16} />
                <span>{user?.email}</span>
              </div>
              <div className="flex items-center space-x-2">
                <Calendar size={16} />
                <span>Joined {user?.createdAt ? new Date(user.createdAt).toLocaleDateString() : 'Recently'}</span>
              </div>
            </div>
          </div>
          <div className="text-right">
            <div className="text-2xl font-bold text-green-400">
              ${((user?.usdBalanceCents || 0) / 100).toFixed(2)}
            </div>
            <div className="text-gray-400 text-sm">Current Balance</div>
          </div>
        </div>
      </div>

      {/* Statistics */}
      <div className="card">
        <h2 className="text-xl font-bold mb-6 flex items-center space-x-2">
          <TrendingUp className="text-blue-400" size={24} />
          <span>Game Statistics</span>
        </h2>
        
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          <div className="text-center">
            <div className="text-2xl font-bold text-blue-400 mb-1">{stats.totalGames}</div>
            <div className="text-gray-400 text-sm">Total Games</div>
          </div>
          
          <div className="text-center">
            <div className="text-2xl font-bold text-green-400 mb-1">{stats.totalWins}</div>
            <div className="text-gray-400 text-sm">Wins</div>
          </div>
          
          <div className="text-center">
            <div className="text-2xl font-bold text-red-400 mb-1">{stats.totalLosses}</div>
            <div className="text-gray-400 text-sm">Losses</div>
          </div>
          
          <div className="text-center">
            <div className="text-2xl font-bold text-yellow-400 mb-1">
              {stats.totalGames > 0 ? `${stats.winRate.toFixed(1)}%` : '0%'}
            </div>
            <div className="text-gray-400 text-sm">Win Rate</div>
          </div>
        </div>
        
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mt-6 pt-6 border-t border-gray-700">
          <div className="text-center">
            <div className="text-2xl font-bold text-purple-400 mb-1">
              ${(stats.totalWagered / 100).toFixed(2)}
            </div>
            <div className="text-gray-400 text-sm">Total Wagered</div>
          </div>
          
          <div className="text-center">
            <div className="text-2xl font-bold text-green-400 mb-1">
              ${(stats.totalWinnings / 100).toFixed(2)}
            </div>
            <div className="text-gray-400 text-sm">Total Winnings</div>
          </div>
          
          <div className="text-center">
            <div className="text-2xl font-bold text-blue-400 mb-1">{stats.longestWalk}s</div>
            <div className="text-gray-400 text-sm">Longest Walk</div>
          </div>
          
          <div className="text-center">
            <div className="text-2xl font-bold text-yellow-400 mb-1">
              ${(stats.biggestWin / 100).toFixed(2)}
            </div>
            <div className="text-gray-400 text-sm">Biggest Win</div>
          </div>
        </div>
      </div>

      {/* Achievements */}
      <div className="card">
        <h2 className="text-xl font-bold mb-6 flex items-center space-x-2">
          <Trophy className="text-yellow-400" size={24} />
          <span>Achievements</span>
        </h2>
        
        <div className="grid md:grid-cols-2 gap-4">
          {achievements.map((achievement) => (
            <div
              key={achievement.id}
              className={`p-4 rounded-lg border ${
                achievement.unlocked
                  ? 'bg-yellow-900/20 border-yellow-500/50'
                  : 'bg-gray-700/50 border-gray-600'
              }`}
            >
              <div className="flex items-center space-x-3">
                <div className={`text-2xl ${achievement.unlocked ? '' : 'grayscale opacity-50'}`}>
                  {achievement.icon}
                </div>
                <div className="flex-1">
                  <h3 className={`font-bold ${achievement.unlocked ? 'text-yellow-400' : 'text-gray-300'}`}>
                    {achievement.name}
                  </h3>
                  <p className="text-gray-400 text-sm">{achievement.description}</p>
                </div>
                {achievement.unlocked && (
                  <div className="text-yellow-400">
                    <Trophy size={20} />
                  </div>
                )}
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Account Actions */}
      <div className="grid md:grid-cols-2 gap-6">
        {/* Account Settings */}
        <div className="card">
          <h2 className="text-xl font-bold mb-4 flex items-center space-x-2">
            <User className="text-gray-400" size={24} />
            <span>Account Settings</span>
          </h2>
          <div className="space-y-3">
            <button className="btn-secondary w-full py-3 text-left">
              Change Email
            </button>
            <button className="btn-secondary w-full py-3 text-left">
              Change Password
            </button>
            <button className="btn-secondary w-full py-3 text-left">
              Download Data
            </button>
          </div>
        </div>

        {/* Game History */}
        <div className="card">
          <h2 className="text-xl font-bold mb-4 flex items-center space-x-2">
            <Clock className="text-gray-400" size={24} />
            <span>Recent Games</span>
          </h2>
          <div className="text-center py-8 text-gray-400">
            <p>No games played yet.</p>
            <button className="text-blue-400 hover:text-blue-300 transition-colors mt-2">
              Play your first game →
            </button>
          </div>
        </div>
      </div>

      {/* Responsible Gaming */}
      <div className="card bg-amber-900/20 border-amber-500/50">
        <h2 className="text-xl font-bold mb-4 flex items-center space-x-2 text-amber-400">
          <Target size={24} />
          <span>Responsible Gaming</span>
        </h2>
        <p className="text-gray-300 mb-4">
          We're committed to promoting responsible gambling. Set limits, take breaks, and remember that gambling should be fun, not a financial solution.
        </p>
        <div className="flex flex-wrap gap-3">
          <button className="btn-secondary text-sm py-2 px-4">
            Set Deposit Limits
          </button>
          <button className="btn-secondary text-sm py-2 px-4">
            Set Time Limits
          </button>
          <button className="btn-secondary text-sm py-2 px-4">
            Self-Exclusion
          </button>
        </div>
      </div>
    </div>
  );
};

export default ProfilePage; 