import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../hooks/useAuth';
import AuthModal from '../components/auth/AuthModal';
import { Play, TrendingUp, Shield, Users } from 'lucide-react';

const LandingPage: React.FC = () => {
  const { isAuthenticated } = useAuth();
  const navigate = useNavigate();
  const [showAuthModal, setShowAuthModal] = useState(false);
  const [authMode, setAuthMode] = useState<'login' | 'register'>('register');

  const handleGetStarted = () => {
    if (isAuthenticated) {
      navigate('/dashboard');
    } else {
      setAuthMode('register');
      setShowAuthModal(true);
    }
  };

  const handlePlayDemo = () => {
    if (isAuthenticated) {
      navigate('/game');
    } else {
      setAuthMode('login');
      setShowAuthModal(true);
    }
  };

  return (
    <>
      <div className="min-h-screen">
        {/* Hero Section */}
        <section className="py-20 text-center">
          <div className="container mx-auto px-4">
            <div className="max-w-4xl mx-auto">
              <h1 className="text-5xl md:text-7xl font-bold mb-6">
                <span className="gradient-text">Dog Walk</span>
                <br />
                <span className="text-white">Gamble</span>
              </h1>
              <p className="text-xl md:text-2xl text-gray-300 mb-8 max-w-3xl mx-auto">
                Walk your virtual dog and cash out before the squirrel appears! 
                The longer you walk, the bigger the payout – but beware of the squirrel!
              </p>
              
              {/* Game Preview */}
              <div className="game-canvas mb-8 max-w-2xl mx-auto relative">
                <div className="absolute inset-0 flex items-center justify-center">
                  <div className="text-center">
                    <div className="text-6xl mb-4 animate-bounce-dog">🐕</div>
                    <p className="text-white font-semibold">Your dog is ready to walk!</p>
                    <div className="mt-4 text-yellow-400 font-bold text-2xl">
                      Multiplier: 2.5x
                    </div>
                  </div>
                </div>
              </div>

              {/* CTA Buttons */}
              <div className="flex flex-col sm:flex-row gap-4 justify-center items-center">
                <button
                  onClick={handleGetStarted}
                  className="btn-primary text-lg px-8 py-4 flex items-center space-x-2"
                >
                  <Play size={20} />
                  <span>{isAuthenticated ? 'Go to Dashboard' : 'Get Started'}</span>
                </button>
                <button
                  onClick={handlePlayDemo}
                  className="btn-secondary text-lg px-8 py-4"
                >
                  {isAuthenticated ? 'Play Now' : 'Login to Play'}
                </button>
              </div>
            </div>
          </div>
        </section>

        {/* Features Section */}
        <section className="py-20 bg-gray-900">
          <div className="container mx-auto px-4">
            <h2 className="text-3xl md:text-4xl font-bold text-center mb-12">
              Why Choose Dog Walk Gamble?
            </h2>
            
            <div className="grid md:grid-cols-3 gap-8 max-w-6xl mx-auto">
              {/* Provably Fair */}
              <div className="card text-center">
                <Shield className="mx-auto mb-4 text-green-400" size={48} />
                <h3 className="text-xl font-bold mb-3">Provably Fair</h3>
                <p className="text-gray-300">
                  Every game outcome is cryptographically verifiable. 
                  You can independently verify the fairness of every round.
                </p>
              </div>

              {/* High Payouts */}
              <div className="card text-center">
                <TrendingUp className="mx-auto mb-4 text-blue-400" size={48} />
                <h3 className="text-xl font-bold mb-3">High Payouts</h3>
                <p className="text-gray-300">
                  Exponential payout curve means bigger rewards for taking risks. 
                  Cash out at the perfect moment to maximize your winnings.
                </p>
              </div>

              {/* Community */}
              <div className="card text-center">
                <Users className="mx-auto mb-4 text-purple-400" size={48} />
                <h3 className="text-xl font-bold mb-3">Active Community</h3>
                <p className="text-gray-300">
                  Compete on leaderboards, unlock new dog breeds, 
                  and join a thriving community of players.
                </p>
              </div>
            </div>
          </div>
        </section>

        {/* How to Play */}
        <section className="py-20">
          <div className="container mx-auto px-4">
            <h2 className="text-3xl md:text-4xl font-bold text-center mb-12">
              How to Play
            </h2>
            
            <div className="max-w-4xl mx-auto">
              <div className="grid md:grid-cols-2 gap-8 items-center">
                <div className="space-y-6">
                  <div className="flex items-start space-x-4">
                    <div className="bg-blue-600 text-white rounded-full w-8 h-8 flex items-center justify-center font-bold">
                      1
                    </div>
                    <div>
                      <h3 className="font-bold mb-2">Place Your Bet</h3>
                      <p className="text-gray-300">Choose how much you want to wager on your dog walk.</p>
                    </div>
                  </div>
                  
                  <div className="flex items-start space-x-4">
                    <div className="bg-blue-600 text-white rounded-full w-8 h-8 flex items-center justify-center font-bold">
                      2
                    </div>
                    <div>
                      <h3 className="font-bold mb-2">Watch the Multiplier Grow</h3>
                      <p className="text-gray-300">Your payout multiplier increases every second your dog walks safely.</p>
                    </div>
                  </div>
                  
                  <div className="flex items-start space-x-4">
                    <div className="bg-blue-600 text-white rounded-full w-8 h-8 flex items-center justify-center font-bold">
                      3
                    </div>
                    <div>
                      <h3 className="font-bold mb-2">Cash Out Before the Squirrel!</h3>
                      <p className="text-gray-300">Click cash out at any time to secure your winnings, but beware – a squirrel could appear!</p>
                    </div>
                  </div>
                </div>
                
                <div className="game-canvas">
                  <div className="absolute inset-0 flex items-center justify-center">
                    <div className="text-center">
                      <div className="text-4xl mb-2">🐕 ➡️ 🐿️</div>
                      <p className="text-white font-semibold">Don't let the squirrel catch you!</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* Footer */}
        <footer className="bg-gray-900 py-8 border-t border-gray-800">
          <div className="container mx-auto px-4 text-center">
            <p className="text-gray-400">
              © 2024 Dog Walk Gamble. Must be 21+ to play. Gamble responsibly.
            </p>
          </div>
        </footer>
      </div>

      {/* Auth Modal */}
      {showAuthModal && (
        <AuthModal
          mode={authMode}
          onClose={() => setShowAuthModal(false)}
          onSwitchMode={() => setAuthMode(authMode === 'login' ? 'register' : 'login')}
        />
      )}
    </>
  );
};

export default LandingPage; 