import React, { useEffect, useState } from 'react';
import { useGameContext } from '../../contexts/GameContext';
import { Clock, DollarSign, TrendingUp } from 'lucide-react';

interface GameCanvasProps {
  betAmount: number;
}

const GameCanvas: React.FC<GameCanvasProps> = ({ betAmount }) => {
  const { gameState } = useGameContext();
  const [backgroundPosition, setBackgroundPosition] = useState(0);
  const [currentScenery, setCurrentScenery] = useState(0);
  const [leashSway, setLeashSway] = useState(0);

  // Park scenery elements that change as you walk
  const sceneryElements = [
    { trees: '🌳🌲🌳', ground: '🌿🌿🌿', extras: '🦋', sky: '☀️' },
    { trees: '🌲🌳🌲', ground: '🌸🌸🌸', extras: '🐝', sky: '⛅' },
    { trees: '🌳🌴🌳', ground: '🌺🌺🌺', extras: '🦆', sky: '🌤️' },
    { trees: '🌲🌲🌳', ground: '🌻🌻🌻', extras: '🐛', sky: '☁️' },
    { trees: '🌳🌳🌲', ground: '🌷🌷🌷', extras: '🦋', sky: '🌈' }
  ];

  // Animate background movement and leash sway when game is active
  useEffect(() => {
    if (!gameState.isGameActive) return;

    const interval = setInterval(() => {
      setBackgroundPosition(prev => prev - 2);
      setLeashSway(() => Math.sin(Date.now() / 400) * 3); // Gentle sway
      
      // Change scenery every 5 seconds
      if (gameState.gameTime > 0 && gameState.gameTime % 5 === 0) {
        setCurrentScenery(prev => (prev + 1) % sceneryElements.length);
      }
    }, 100);

    return () => clearInterval(interval);
  }, [gameState.isGameActive, gameState.gameTime]);

  const currentScene = sceneryElements[currentScenery];
  const currentEarnings = ((betAmount * gameState.currentMultiplier) / 100).toFixed(2);
  const profit = ((betAmount * (gameState.currentMultiplier - 1)) / 100).toFixed(2);

  const getDogAnimation = () => {
    if (!gameState.isGameActive) {
      return { animation: 'float 2s ease-in-out infinite' };
    }
    return { animation: 'bounce-dog 0.8s ease-in-out infinite' };
  };

  const getSquirrelAnimation = () => {
    if (!gameState.squirrelEvent) return { display: 'none' };
    return {
      right: '5%',
      animation: 'squirrel-run 2s ease-out',
      display: 'block'
    };
  };

  return (
    <div className="relative w-full h-96 bg-gradient-to-b from-sky-400 via-sky-200 to-green-300 rounded-xl overflow-hidden shadow-2xl">
      {/* Game HUD - Top Overlay */}
      <div className="absolute inset-x-0 top-0 z-30">
        <div className="flex justify-between items-start p-4">
          {/* Left side - Game status */}
          <div className="bg-black bg-opacity-60 rounded-lg px-4 py-2 backdrop-blur-sm">
            <div className="flex items-center space-x-2">
              <div className={`w-2 h-2 rounded-full ${
                gameState.connectionStatus === 'connected' ? 'bg-green-400' : 
                gameState.connectionStatus === 'connecting' ? 'bg-yellow-400' : 'bg-red-400'
              }`}></div>
              <span className="text-white text-sm font-medium">
                {gameState.isGameActive ? 'Walking...' : 'Ready'}
              </span>
            </div>
          </div>

          {/* Right side - Timer and earnings */}
          <div className="space-y-2">
            {/* Timer */}
            <div className="bg-black bg-opacity-60 rounded-lg px-4 py-2 backdrop-blur-sm flex items-center space-x-2">
              <Clock size={16} className="text-blue-400" />
              <span className="text-white font-bold text-lg">
                {gameState.gameTime}s
              </span>
            </div>
            
            {/* Current Value */}
            <div className="bg-gradient-to-r from-green-600 to-green-500 rounded-lg px-4 py-2 shadow-lg">
              <div className="flex items-center space-x-1">
                <DollarSign size={16} className="text-white" />
                <span className="text-white font-bold text-lg">
                  ${currentEarnings}
                </span>
              </div>
              <div className="text-green-100 text-xs">
                Current Value
              </div>
            </div>

            {/* Multiplier */}
            <div className="bg-gradient-to-r from-purple-600 to-purple-500 rounded-lg px-4 py-2 shadow-lg">
              <div className="flex items-center space-x-1">
                <TrendingUp size={16} className="text-white" />
                <span className="text-white font-bold text-lg">
                  {gameState.currentMultiplier.toFixed(2)}x
                </span>
              </div>
              <div className="text-purple-100 text-xs">
                Multiplier
              </div>
            </div>

            {/* Profit */}
            {gameState.isGameActive && parseFloat(profit) > 0 && (
              <div className="bg-gradient-to-r from-yellow-600 to-yellow-500 rounded-lg px-4 py-2 shadow-lg animate-pulse">
                <div className="flex items-center space-x-1">
                  <span className="text-white text-sm">+${profit}</span>
                </div>
                <div className="text-yellow-100 text-xs">
                  Profit
                </div>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Sky background with weather */}
      <div className="absolute inset-0 bg-gradient-to-b from-blue-400 via-sky-300 to-green-300">
        <div className="absolute top-8 right-16 text-4xl animate-float">
          {currentScene.sky}
        </div>
      </div>
      
      {/* Moving clouds */}
      <div 
        className="absolute top-8 w-full h-16 flex space-x-12 text-3xl opacity-80"
        style={{ transform: `translateX(${backgroundPosition * 0.2}px)` }}
      >
        <span>☁️</span>
        <span>⛅</span>
        <span>☁️</span>
        <span>☁️</span>
        <span>⛅</span>
        <span>☁️</span>
      </div>

      {/* Background mountains/trees (far) */}
      <div 
        className="absolute bottom-40 w-full h-20 flex space-x-6 text-2xl opacity-40"
        style={{ transform: `translateX(${backgroundPosition * 0.3}px)` }}
      >
        <span>🏔️</span>
        <span>🌲</span>
        <span>🏔️</span>
        <span>🌳</span>
        <span>🏔️</span>
        <span>🌲</span>
        <span>🏔️</span>
      </div>

      {/* Middle ground trees */}
      <div 
        className="absolute bottom-28 w-full h-24 flex space-x-8 text-5xl opacity-70"
        style={{ transform: `translateX(${backgroundPosition * 0.6}px)` }}
      >
        {currentScene.trees.split('').map((tree, i) => (
          <span key={`tree-${i}`} className="animate-float" style={{ animationDelay: `${i * 0.2}s` }}>
            {tree}
          </span>
        ))}
        <span className="animate-float" style={{ animationDelay: '0.6s' }}>
          {currentScene.extras}
        </span>
        {currentScene.trees.split('').map((tree, i) => (
          <span key={`tree-${i + 10}`} className="animate-float" style={{ animationDelay: `${(i + 3) * 0.2}s` }}>
            {tree}
          </span>
        ))}
      </div>

      {/* Foreground flowers and details */}
      <div 
        className="absolute bottom-20 w-full h-12 flex space-x-4 text-2xl opacity-90"
        style={{ transform: `translateX(${backgroundPosition * 1.2}px)` }}
      >
        {currentScene.ground.split('').map((flower, i) => (
          <span key={`flower-${i}`}>{flower}</span>
        ))}
        {currentScene.ground.split('').map((flower, i) => (
          <span key={`flower-${i + 10}`}>{flower}</span>
        ))}
        {currentScene.ground.split('').map((flower, i) => (
          <span key={`flower-${i + 20}`}>{flower}</span>
        ))}
      </div>

      {/* Park path */}
      <div className="absolute bottom-0 w-full h-20 bg-gradient-to-t from-amber-700 via-amber-600 to-amber-500 opacity-80"></div>
      
      {/* Path texture and details */}
      <div 
        className="absolute bottom-2 w-full h-12 flex space-x-6 text-lg opacity-60"
        style={{ transform: `translateX(${backgroundPosition * 1.8}px)` }}
      >
        <span>🪨</span>
        <span>🍂</span>
        <span>🪨</span>
        <span>🍂</span>
        <span>🪨</span>
        <span>🍂</span>
        <span>🪨</span>
        <span>🍂</span>
      </div>

      {/* Owner's hand with leash */}
      {gameState.isGameActive && (
        <div className="absolute bottom-16 left-8 z-20">
          {/* Hand */}
          <div className="text-4xl" style={{ transform: `rotate(${leashSway}deg)` }}>
            🤚
          </div>
          {/* Leash line */}
          <svg 
            className="absolute top-4 left-6" 
            width="100" 
            height="40"
            style={{ transform: `rotate(${leashSway * 0.5}deg)` }}
          >
            <path
              d="M 0 0 Q 30 15 60 5"
              stroke="#8B4513"
              strokeWidth="3"
              fill="none"
              strokeLinecap="round"
            />
          </svg>
        </div>
      )}

      {/* Dog walking */}
      <div
        className="absolute bottom-16 left-20 text-7xl transition-all duration-300 z-15"
        style={getDogAnimation()}
      >
        🐕
      </div>

      {/* Dog collar with leash attachment */}
      {gameState.isGameActive && (
        <div className="absolute bottom-20 left-[5.5rem] w-2 h-2 bg-red-600 rounded-full z-10"></div>
      )}

      {/* Squirrel appears */}
      <div
        className="absolute bottom-16 text-6xl z-25"
        style={getSquirrelAnimation()}
      >
        🐿️
      </div>

      {/* Walking effects */}
      {gameState.isGameActive && (
        <>
          {/* Dust clouds from walking */}
          <div className="absolute bottom-12 left-24 text-xl opacity-70 animate-pulse">
            💨
          </div>
          <div className="absolute bottom-10 left-28 text-lg opacity-50 animate-pulse" style={{ animationDelay: '0.3s' }}>
            💨
          </div>
          
          {/* Footprints trail */}
          <div 
            className="absolute bottom-12 flex space-x-3 text-sm opacity-40"
            style={{ transform: `translateX(${backgroundPosition * 1.5}px)` }}
          >
            <span>🐾</span>
            <span>🐾</span>
            <span>🐾</span>
            <span>🐾</span>
            <span>🐾</span>
          </div>
        </>
      )}

      {/* Game status overlays */}
      <div className="absolute inset-0 flex items-center justify-center pointer-events-none z-10">
        {!gameState.isGameActive && !gameState.squirrelEvent && (
          <div className="text-center bg-black bg-opacity-50 rounded-xl p-8 backdrop-blur-sm animate-slide-in">
            <div className="text-6xl mb-4 animate-bounce-dog">🐕</div>
            <p className="text-white font-bold text-2xl mb-2">Ready for Adventure!</p>
            <p className="text-gray-300">Click "Start Walking" to begin</p>
          </div>
        )}
        
        {gameState.squirrelEvent && (
          <div className="text-center bg-red-900 bg-opacity-80 rounded-xl p-8 backdrop-blur-sm animate-slide-in border-2 border-red-400">
            <div className="text-6xl mb-4 animate-wiggle">🐿️</div>
            <p className="text-red-100 font-bold text-2xl mb-2">Squirrel Spotted!</p>
            <p className="text-red-200">Your walk has ended!</p>
          </div>
        )}
      </div>

      {/* Risk level indicator at bottom */}
      {gameState.isGameActive && (
        <div className="absolute bottom-2 left-1/2 transform -translate-x-1/2 z-20">
          <div className="bg-black bg-opacity-60 rounded-full px-4 py-1 backdrop-blur-sm">
            <div className="flex items-center space-x-2">
              <div className={`w-2 h-2 rounded-full ${
                gameState.gameTime < 5 ? 'bg-green-400' :
                gameState.gameTime < 10 ? 'bg-yellow-400' :
                gameState.gameTime < 20 ? 'bg-orange-400' : 'bg-red-400'
              } animate-pulse`}></div>
              <span className="text-white text-xs font-medium">
                {gameState.gameTime < 5 ? 'Low Risk' :
                 gameState.gameTime < 10 ? 'Medium Risk' :
                 gameState.gameTime < 20 ? 'High Risk' : 'EXTREME RISK'}
              </span>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default GameCanvas; 