import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../../hooks/useAuth';
import AuthModal from '../auth/AuthModal';
import { User, Wallet, Trophy, Home, LogOut } from 'lucide-react';

const Navbar: React.FC = () => {
  const { user, isAuthenticated, logout } = useAuth();
  const [showAuthModal, setShowAuthModal] = useState(false);
  const [authMode, setAuthMode] = useState<'login' | 'register'>('login');
  const navigate = useNavigate();

  const handleAuthClick = (mode: 'login' | 'register') => {
    setAuthMode(mode);
    setShowAuthModal(true);
  };

  const handleLogout = async () => {
    await logout();
    navigate('/');
  };

  return (
    <>
      <nav className="bg-gray-900 border-b border-gray-800 sticky top-0 z-40">
        <div className="container mx-auto px-4">
          <div className="flex items-center justify-between h-16">
            {/* Logo */}
            <Link to="/" className="flex items-center space-x-2">
              <span className="text-2xl">🐕</span>
              <span className="text-xl font-bold gradient-text">Dog Walk Gamble</span>
            </Link>

            {/* Navigation Links */}
            <div className="hidden md:flex items-center space-x-6">
              {isAuthenticated ? (
                <>
                  <Link
                    to="/dashboard"
                    className="flex items-center space-x-1 text-gray-300 hover:text-white transition-colors"
                  >
                    <Home size={18} />
                    <span>Dashboard</span>
                  </Link>
                  <Link
                    to="/game"
                    className="flex items-center space-x-1 text-gray-300 hover:text-white transition-colors"
                  >
                    <span>🎮</span>
                    <span>Play</span>
                  </Link>
                  <Link
                    to="/leaderboard"
                    className="flex items-center space-x-1 text-gray-300 hover:text-white transition-colors"
                  >
                    <Trophy size={18} />
                    <span>Leaderboard</span>
                  </Link>
                  <Link
                    to="/profile"
                    className="flex items-center space-x-1 text-gray-300 hover:text-white transition-colors"
                  >
                    <User size={18} />
                    <span>Profile</span>
                  </Link>
                </>
              ) : (
                <Link to="/" className="text-gray-300 hover:text-white transition-colors">
                  Home
                </Link>
              )}
            </div>

            {/* Auth/User Section */}
            <div className="flex items-center space-x-4">
              {isAuthenticated ? (
                <>
                  {/* User Balance */}
                  <div className="flex items-center space-x-2 bg-gray-800 px-3 py-1 rounded-lg">
                    <Wallet size={16} className="text-green-400" />
                    <span className="text-green-400 font-semibold">
                      ${((user?.usdBalanceCents || 0) / 100).toFixed(2)}
                    </span>
                  </div>

                  {/* User Menu */}
                  <div className="flex items-center space-x-2">
                    <span className="text-gray-300 hidden sm:block">
                      Welcome, {user?.username}
                    </span>
                    <button
                      onClick={handleLogout}
                      className="flex items-center space-x-1 text-gray-400 hover:text-white transition-colors"
                    >
                      <LogOut size={18} />
                      <span className="hidden sm:block">Logout</span>
                    </button>
                  </div>
                </>
              ) : (
                <div className="flex items-center space-x-3">
                  <button
                    onClick={() => handleAuthClick('login')}
                    className="text-gray-300 hover:text-white transition-colors"
                  >
                    Login
                  </button>
                  <button
                    onClick={() => handleAuthClick('register')}
                    className="btn-primary"
                  >
                    Sign Up
                  </button>
                </div>
              )}
            </div>
          </div>
        </div>
      </nav>

      {/* Auth Modal */}
      {showAuthModal && (
        <AuthModal
          mode={authMode}
          onClose={() => setShowAuthModal(false)}
          onSwitchMode={() => setAuthMode(authMode === 'login' ? 'register' : 'login')}
        />
      )}
    </>
  );
};

export default Navbar; 